<template>
  <div class="register-page">
    <img src="../../assets/images/logo.png" alt="logo" class="logo">
    <div class="register-form">
      <h1>NG K</h1>
      <v-form @submit.prevent="checkComfirmPassword">
        <div class="form-input">
          <label for="username">Tn:</label>
          <v-text-field class="input" outlined type="text" id="username" v-model="username" required />
        </div>
        <div class="form-input">
          <label for="email">Email:</label>
          <v-text-field class="input" outlined type="text" id="email" v-model="email" required />
        </div>
        <div class="form-input">
          <label for="password">Mt khu:</label>
          <v-text-field
              outlined
              :type="showPassword ? 'text' : 'password'"
              id="password"
              v-model="password"
              :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
              @click:append="togglePasswordVisibility('password')"
              class="input"
              required />
        </div>
        <div class="form-input">
          <label for="confirm-password">Nhp li mt khu:</label>
          <v-text-field
              outlined
              :type="showConfirmPassword ? 'text' : 'password'"
              id="confirm-password"
              v-model="confirmPassword"
              :append-icon="showConfirmPassword ? 'mdi-eye' : 'mdi-eye-off'"
              @click:append="togglePasswordVisibility('confirmPassword')"
              class="input"
              required />
        </div>
        <div class="button">
          <v-btn type="submit" class="register-btn">ng k</v-btn>
          <div>
            Bn  c ti khon ? <router-link class="login-router" to="login">ng nhp ngay</router-link>
          </div>
        </div>
      </v-form>
      <v-dialog v-model="showTwoFactorAuth" max-width="500px" >
        <TwoFactorAuth :qrImage="qrImage" @verify="registerUser" />
      </v-dialog>
    </div>
  </div>
</template>

<script>
import apiConfig from '@/apiConfig';
import { validateEmail } from '@/utils/validators';
import QRCode from 'qrcode';
import TwoFactorAuth from '@/components/TwoFactorAuth.vue';
import { validatePassword } from '@/utils/validators';

export default {
  name: 'RegisterPage',
  components: {
    TwoFactorAuth,
  },
  data() {
    return {
      username: '',
      email: '',
      password: '',
      confirmPassword: '',
      showConfirmPassword: false,
      showPassword: false,
      qrImage: null,
      showTwoFactorAuth: false,
    };
  },
  methods: {
    async checkComfirmPassword() {
      if (!this.email || !this.password) {
        this.$toast.error('Vui lng nhp y  thng tin!');
        return;
      }
      if (!validateEmail(this.email)) {
        this.$toast.error('Email khng ng nh dng!');
        return;
      }
      if (!validatePassword(this.password)) {
        this.$toast.error('Mt khu phi c t nht 6 k t, cha t nht 1 ch vit hoa v 1 k t c bit!');
        return;
      }
      if (this.password !== this.confirmPassword) {
        this.$toast.warning('Mt khu v xc nhn mt khu khng khp!');
        return;
      }
      try {
        const response = await apiConfig.getQrCode({
          name: this.username,
          email: this.email,
          password: this.password,
        });
        if (response.data.status === 200) {
          const otpauthUrl = response.data.qr_image;
          this.qrImage = await QRCode.toDataURL(otpauthUrl);
          this.qrImageSecret = response.data.google2fa_secret; // Store the secret
          this.showTwoFactorAuth = true;
          this.$toast.success('Vui lng qut m QR v nhp m xc thc 2FA!');
        } else if (response.data.status === 409) {
          this.$toast.warning(response.data.message);
        } else {
          this.$toast.error('ng k tht bi, vui lng th li!');
        }
      } catch (error) {
        this.$toast.error('ng k tht bi, vui lng th li!');
      }
    },
    async registerUser(twoFactorCode) {
      try {
        const response = await apiConfig.register({
          name: this.username,
          email: this.email,
          password: this.password,
          google2fa_code: twoFactorCode,
          google2fa_secret: this.qrImageSecret, // Include the google2fa_secret
        });
        if (response.data.status === 200) {
          this.$toast.success('ng k thnh cng!');
          this.$router.push('/login');
        } else {
          this.$toast.error('M xc thc 2FA khng hp l!');
        }
      } catch (error) {
        this.$toast.error('Xc thc 2FA tht bi, vui lng th li!');
      }
    },
    togglePasswordVisibility(field) {
      if (field === 'password') {
        this.showPassword = !this.showPassword;
      } else if (field === 'confirmPassword') {
        this.showConfirmPassword = !this.showConfirmPassword;
      }
    },
  },
};
</script>

